import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'

const home = r => require.ensure([], () => r(require('../page/home/home')), 'home')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        {
          path: '',
          redirect: '/home'
        },
        {
          path: '/home',
          component: home
        }
      ]
    }
  ]
})



